// Progress Card
// Inherits global card styling.
// Used on Topic List / Library Page and the Topic Detailed Page.

// There are two types of progress cards:
//
// .card-progress__topic   - found on library page featuring header, footer, content and progress indicator parts.
// .card-progress__lessons - found at the top of topic detailed page featuring just the progress indicator parts

$progress-header-small: 6.4rem;
$progress-header-large: 8.8rem;

.card-progress {
    position: relative;
    max-width: 59rem;
}

.card-progress__header {
    background-color: map-get($color, topicsHeaderBg);
    color: white;
    min-height: $progress-header-small;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 map-get($keyline, small);
    border-radius: 0.3rem 0.3rem 0 0;

    @include bp(tab-up) {
        min-height: $progress-header-large;

        padding: 0 map-get($keyline, large);
    }
}

.card-progress__header__title {
    @extend %primary-font-light;
    font-size: 2rem;
    line-height: 3.2rem;
    padding: 1.6rem 0;

    @include bp(tab-up) {
        font-size: 2.4rem;
    }
}

.card-progress__header__title-icon {
    display: block;
    max-width: 2.4rem;
    max-height: 2.4rem;
    width: auto;
    height: auto;
    margin-right: 2rem;
}

.card-progress__header__title-text {
    vertical-align: middle;
    display: inline-block;
}

.card-progress__content {
    min-height: 16rem;
    padding: 0 map-get($cardPadding, ml);

    display: flex;
    flex-direction: column;
    justify-content: center;

    @include bp(tab-up) {
        padding: 0 map-get($cardPadding, l);
        min-height: 19rem;
    }
}

.card-progress__actions {
    height: 5.6rem;
    padding: 0 map-get($cardPadding, ml);

    @include bp(tab-up) {
        padding: 0 map-get($cardPadding, l);
        height: 7.2rem;
    }
}


.progress__description {
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: map-get($black, secondary);
    @extend %primary-font-light;

    @include bp(tab-up) {
        font-size: 1.8rem;
    }
}

.progress-stats {
    display: flex;
    flex-direction: row;
    align-self: stretch;
    align-content:  center;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.9rem;

    @include bp(tab-up) {
        margin-top: 3.6rem;
    }
}

.progress-stats__stat {
    @extend %secondary-font-bold;
    font-size: 1.5rem;
    line-height: 2.4rem;
    color: map-get($black, secondary);
    margin-bottom: 1.5rem;

    align-self: center;
    align-content: center;
    vertical-align: middle;

    @include bp(tab-up) {
        margin-bottom: 1.8rem;
    }
}

.progress-stats__icon {
    margin-right: 1.7rem;
}

.progress__text {
    display: none;

    @include bp(tab-up) {
        display: flex;
        justify-content: center;
        flex-direction: column;
        margin: 0 4rem 0 0;
    }
}

.progress__label {
    display: none;

    @include bp(tab-up) {
        display: block;
        @extend %primary-font-light;
        font-size: 1.5rem;
        color: map-get($color, action-dark-54);
        line-height: 1.8rem;
    }
}

.progress__percent {
    @extend %primary-font-light;
    font-size: 2.4rem;
    color: map-get($color, action-dark-54);
    line-height: 2.8rem;
    margin-top: 0.6rem;
}

.progress__result .img-icon {
    display: block;
    max-width: 1.9rem;
    max-height: 1.9rem;
    width: auto;
    height: auto;

    @include bp(tab-up) {
        max-width: 2.8rem;
        max-height: 2.8rem;
        width: auto;
        height: auto;
    }
}


// Specific to how many lessons are completed in the unit progress Indicator Card
// Found on topic detailed page.
.card-progress__lessons {
    max-width:100%;

    .card-progress__content {
        min-height: 10.2rem;
        padding: 1rem 2.4rem;

        @include bp(tab-up) {
            min-height: 12rem;
            flex-direction: row;
        }
    }

    .progress__indicator {
        margin-bottom: 2rem;

        @include bp(tab-up) {
            margin-bottom: 0;
            display: flex;
            flex: 1;
            flex-direction: row;
            align-items: center;
        }
    }

    .progress-stats {
        display: block;
        margin-top:0;

        @include bp(tab-up) {
            display: none;
        }
    }
}