// Diagnostics Card - Options
// Inherits global card styling.
// Used on Diagnostic Screens.
.card-diag {
    background: white;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    outline: none;

    &:first-of-type {
        border-top: 0.1rem solid #DADADA;
    }

    &:not(:last-of-type) {
        border-bottom: 0.1rem solid #DADADA;
    }

    &:hover,
    &:focus {
        .card-diag__illo {
            filter: grayscale(50%);
            opacity: 1;
        }
    }

    &.is-selected {
        .card-diag__illo {
            filter: grayscale(0);
            opacity: 1;
        }
    }

    .no-cssfilters & {
        &:hover {
            .card-diag__illo {
                opacity: 0.5;
            }
        }

        &.is-selected {
            .card-diag__illo {
                opacity: 1;
            }
        }
    }
}

.card-diag__illo {
    flex: 0 0 7.2rem;
    align-self: flex-start; // If the option stretches in height, the image anchors to the top.

    .no-cssfilters & {
        opacity: 0.2;
        transition: opacity 0.2s;
    }

    .cssfilters & {
        filter: grayscale(100%);
        opacity: 0.65;
        transition: all 0.2s;
    }
}

.card-diag__contents {
    display: flex;
    padding: 0 2rem;
    flex-grow: 1;
}

.card-diag__option {
    @extend %primary-font-light;
    color: map-get($color, diagnosticsOptionDeselected);
    font-size: 1.4rem;
    line-height: 1.285714286;
    padding: 1.2rem 0;

    @include bp(tab-up) {
        font-size: 1.8rem;
    }

    .is-selected & {
        color: palette(sanMarino, base);
    }
}

// Diagnostics Card - Plan
$progress-header-small: 7.2rem;
$progress-header-large: 7.2rem;

.card-diag-result {
    position: relative;
    max-width: $ctn-max-width;
    margin: auto;
}

.card-diag-result__header {
    background-color: white;
    color: map-get($color, action-dark-87);
    min-height: $progress-header-small;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid map-get($color, divider);
    background-clip: content-box;
    border-radius: 0.3rem 0.3rem 0 0;
    padding: 0 map-get($keyline, small);

    @include bp(tab-up) {
        min-height: $progress-header-large;
        padding: 0 map-get($keyline, large);
    }
}

.card-diag__result__header--contents {
    padding: 0 map-get($keyline, small);
    width: 100%;
    display: flex;
    flex-direction: row;
    align-content: stretch;
    justify-content: space-between;

    @include bp(tab-up) {
        padding: 0 map-get($keyline, large);
    }
}

.card-diag-result__progress-stats {
    margin-bottom: 0;

    @include bp(tab-up) {
        margin-bottom: 0;
    }
}

.card-diag-result__header--collapsed {
    position: relative;
    padding: 0;
    border-radius: 0.3rem;

    @include bp(tab-max) {
        border-bottom: 0;
    }

    @include bp(tab-up) {
        box-shadow: map-get($shadow, 1);
        margin: auto;
        padding:0;
    }

    .card-diag-result__progress-stats.text-with-icon {
        @include bp(tab-max) {
            display: none;
        }
    }

    &.is-locked {
        background: #e6e6e6;

        .card-diag-result__header__title {

            margin-left: 2rem;
            margin-right: 5rem;

            @include bp(tab-up) {
                margin: auto;
            }
        }

        .card-diag-result__header__title-icon {
            color: #9c9c9c;
        }

        .icon--info {
            color: #696969;
        }

        .card-diag-result__progress-stats {
            margin:0;
            position: absolute;
            top: 2.5rem;
            right: 1.6rem;
            left: auto;
        }
    }
}

.card-diag-result__header--hidden {
    display: none;
}

.card-diag-result__header__title {
    padding: 2rem 0;
}

.card-diag-result__header__title-icon {
    width: 2.4rem;
    height: auto;
    margin-right: 2rem;
}

.card-diag-result__header__title-text {
    display: inline-block;
    vertical-align: middle;

    @extend %primary-font-light;
    color: map-get($color, action-dark-87);
    font-size: 2rem;
    line-height: 3.2rem;
}

.card-diag-result__progress-stats {
    @include bp(tab-max) {
        margin: 0;
        position: absolute;
        top: 9.8rem;
        left: 1.8rem;
    }
}

.card-diag-result__content {
    display: flex;
    flex-direction: column-reverse;

    @include bp(tab-up) {
        flex-direction: row;
        min-height: 39.5rem;
    }
}

.card-diag-result__play-icon.material-icons {
    font-size: 7rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    z-index: map-get($z-index, video-play-icon);
    color: white;

    @include bp(tab-up) {
        transform: translate(-50%, calc(-50% - 4rem)); // Negating half the height of the overlying lesson bar.
    }
}

.card-diag-result__thumb-ctn {
    display: none;

    @include bp(tab-up) {
        display: block;
        position: relative;
        width: 100%;
        flex: 1 1 50%;
        align-self: flex-start;

        &::before {
            content: '';
            display: block;
            height: 0;
            width: 100%;
            padding-bottom: 56.25%;
        }
    }

    .card-diag-result__actions {
        @include bp(tab-up) {
            margin-top: 2.1rem;
        }
    }
}

.card-diag-result__thumb-ctn--inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.card-diag-result__thumb {
    max-width: 100%;
    display: block;
}

.card-diag-result__thumb-info {
    background: map-get($color, action-dark-54);
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.6rem;
    height: 6.4rem;
    border-radius: 0 0 0 0.3rem;

    @include bp(tab-up) {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 8rem;
        padding: 0 2.6rem;
    }
}

.card-diag-result__video-name {
    font-size: 1.6rem;
    color: white;
    line-height: 1.9rem;
}

.card-diag-result__icon--query_builder {
    margin-right: 1.1rem;
}

.card-diag-result__copy {
    padding: 6.3rem 1.8rem 3.2rem;
    max-width: 49.8rem;
    width: 100%;
    display: flex;
    flex: 1 1 50%;
    flex-direction: column;

    @include bp(tab-up) {
        padding: 5.1rem 3.2rem 2.2rem 3.6rem;
    }
}

.card-diag-result__actions {
    display:none;

    @include bp(tab-up) {
        display: block;
        position: static;
        text-align: left;
        margin: auto 0 0 0;
    }
}

.card-diag-result__action.text-with-icon {
    color: map-get($color, secondaryBlue);

    @include bp(tab-up) {
        margin-left: -0.8rem;
    }
}

.card-diag-result__heading {
    display: none;

    @include bp(tab-up) {
        display: block;
        font-size: 3.2rem;
        color: black;
        line-height: 3.6rem;
        margin-bottom: 3.6rem;
    }
}

.card-diag-result__intro {
    font-size: 1.8rem;
    color: map-get($color, action-dark-87);
    line-height: 2.8rem;
}

.card-diag-result__lesson-card {
    display: block;
    max-width: 100%;

    .card-diag-result__thumb {
        position: absolute;
        top:0;
        left:0;
        border-radius: 0.3rem 0.3rem 0;
    }

    .card-lesson__content {
        min-height: 12.5rem;
    }

    @include bp(tab-up) {
        display: none;
    }
}