$subpage-header-color: #0288d1; //blue
$subpage-header-min: 28.1rem;
$subpage-content-maxwidth: 79.4rem;
$subpage-content-offset-sm: 2rem;
$subpage-content-offset-lrg: 3.1rem;


$subpage-tabbed-content-with-image-offset-sm: 11.1rem;
$subpage-tabbed-content-with-image-offset-lrg: 4.8rem;

$subpage-tabbed-content-offset-sm: 8.2rem;
$subpage-tabbed-content-offset-lrg: 10.1rem;

$subpage-card-content-padding: (
    mobileUpward: 2rem,
    tabletUpward: 3rem
);

.subpage {
    background: map-get($color, subpage-bg);
}

.subpage-content-wrapper {
    position: relative;
}

// State: Hero - used for headers that need a hero image attached
.subpage-wrapper--hero {
    .subpage-header {
        padding: 0;
        min-height: 0;
    }
    .subpage-header__inner {
        @include bp(tab-up) {
            display: flex;
            flex-direction: row;
            align-content: stretch;
            flex: 1 1 auto;
            min-height: 0;
        }
    }

    .subpage-header__content {
        padding: 7rem map-get($keyline, small) 3rem;

        @include bp(tab-up) {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 0rem map-get($keyline, small) 3rem;
            min-height: 28.1rem;
        }
    }
}

// State: Offset - used for content that needs to be positioned over hero/header info
.subpage-wrapper--offset {

}

// State: Tabbed - for use with tabbed accordions
.subpage-wrapper--tabbed {
    padding: 0;
}


.subpage-header {
    position: relative;
    padding: 2.4rem 0 3.4rem 0;
    z-index: 0;
    background-color: palette(sanMarino, base);
    color: palette(white, base);

    &#{'--subtitle'} {
        padding-bottom: 4.3rem;

        @include bp(tab-up) {
            padding-bottom: 3.8rem;
        }        
    }

    &#{'--tabs'} {
        padding-bottom: 6.2rem;
        
        &#{'--card'} {
            padding-bottom: 6.2rem;
        }
    }

    @include bp(tab-up) {
        padding: 5.6rem 0 6.3rem 0;

        &#{'--subtitle'} {

            @include bp(tab-up) {
                padding-bottom: 5.2rem;
            }        
        }

        &#{'--card'} {
            padding-bottom: 8.3rem;
        }

        &#{'--tabs'} {
            padding-bottom: 6.7rem;
            
            &#{'--card'} {
                padding-bottom: 12.3rem;
            }
        }

    }
}

.subpage-header__content {
    width: 100%;
    max-width: 103.6rem;
    margin: 0 auto;
    padding: 0 2rem;

    @include bp(tab-up) {
        padding: 0 3rem;
    }
}

.subpage-header__title,
.subpage-header__description {
    max-width: 79.4rem;
    color: palette(white, base);
}

.subpage-header__title {
    @extend %secondary-font-light;
    font-size: 2.2rem;
    line-height: (35 / 22);

    @include bp(tab-up) {
        font-size: 2.8rem;
        line-height: (38 / 28);

    }

    @at-root{

        .subpage-header--subtitle & {

            margin: 0 0 0.6rem 0;

            @include bp(tab-up) {
                margin: 0 0 1.4rem 0;

            }
        }
    }
}

.subpage-header__description {
    @extend %primary-font-light;
    font-size: 1.6rem;
    line-height: (23 / 16);
}

.subpage-content {
    max-width: 85.4rem;
    margin: 0 auto 4rem;
    position: relative;

    @include bp(tab-up) {
        margin-bottom: 6rem;
        padding: 0 3rem;
    }

    &#{'--tabs'} {
        margin-top: -4.8rem;
        
        &#{'--card'} {
            margin-top: -4.8rem;

            md-tabs-wrapper {
                padding: 0 0.2rem;
            }

            md-card {
                margin: 0;
            }
        }
    }

    @include bp(tab-up) {

        &#{'--card'} {
            margin-top: -4.8rem;
        }

        &#{'--tabs'} {
            
            &#{'--card'} {
                margin-top: -11rem;

                md-tabs-wrapper {
                    padding: 0;
                }
            }
        }
    }
}

.subpage-content--padded {
    padding-left: map-get($subpage-card-content-padding, mobileUpward);
    padding-right: map-get($subpage-card-content-padding, mobileUpward);

    @include bp(tab-up) {
        padding-left: map-get($subpage-card-content-padding, tabletUpward);
        padding-right: map-get($subpage-card-content-padding, tabletUpward);
    }
}

/**
 * Used for areas of text that need to be padded on mobile
 * but sit flush with the grid from tablet upward.
 *
 * Example:
 * <div class="subpage-content__padded-copy">
 *     <h2 class="card-group--partners__name">{{ partner_group.name }}</h2>
 * </div>
 *
 * @author Anthony Hastings
 */
.subpage-content__padded-copy {
    padding-left: map-get($subpage-card-content-padding, mobileUpward);
    padding-right: map-get($subpage-card-content-padding, mobileUpward);

    @include bp(tab-up) {
        padding-left: 0;
        padding-right: 0;
    }
}

.subpage-content__subtitle {
    margin: 4.2rem 0 3rem;
}
