// Tool Card
// Inherits global card styling.
// Used on Tools Page.

.card--tool__content {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;

    // If a card gets too tall it will force the parallel card in the grid to be the same height as it.
    // If this happens the other card looks malformed. To combat this, we make the content area fill the void.
    flex-grow: 1;

    padding-top: 2.6rem;
    padding-bottom: 1.7rem;
    min-height: 15.6rem;

    @include bp(tab-up) {
        padding-top: 3.2rem;
        padding-bottom: 2.2rem;
        min-height: 16.3rem;
    }
}

.card--tool__icon  {
    display: block;
    flex: 0 0 32px;
}

.card--tool__details {
    flex: 1 1 auto;
    margin-left: 1.6rem;
}

.card--tool__name {
    @extend %primary-font-normal;
    color: palette(mineShaft, base);
    font-size: 1.8rem;
    line-height: (24 / 18);
    margin-bottom: 1.7rem;

    @include bp(tab-up) {
        margin-bottom: 1.2rem;
    }
}

.card--tool__summary {
    @extend %primary-font-light;
    color: palette(black, base);
    font-size: 1.6rem;
    line-height: (24 / 16);
}

.card--tool__actions {
    height: 5.6rem;

    @include bp(tab-up) {
        height: 7.4rem;
    }
}

.card-tool__action {
    @extend %primary-font-med;
    font-size: 1.4rem;
}