// Font Family Extends

$primary-font-family: Roboto, Arial, sans-serif;
$secondary-font-family: "Roboto Slab", Georgia, serif;

%primary-font-light {
    font-family: $primary-font-family;
    font-weight: 300;
}

%primary-font-normal {
    font-family: $primary-font-family;
    font-weight: 400;
}

%primary-font-med {
    font-family: $primary-font-family;
    font-weight: 500;
}

%primary-font-bold {
    font-family: $primary-font-family;
    font-weight: 700;
}

%secondary-font-light {
    font-family: $secondary-font-family;
    font-weight: 300;
}

%secondary-font-normal {
    font-family: $secondary-font-family;
    font-weight: 400;
}

%secondary-font-bold {
    font-family: $secondary-font-family;
    font-weight: 700;
}




html, body {
    -webkit-font-smoothing: antialiased;
}

html {
    font-size: 62.5%;
    line-height: 1;
}

body {
    font-size: 1.6rem;
    font-weight: normal;
    @extend %primary-font-light;
    line-height: 1;
    color: map-get($black, text);
}

h1, h2, h3, h4 {
    text-rendering: optimizeLegibility;
    margin: 0;
    @extend %primary-font-light;
}

a {
    text-decoration: none;
}

p {
    font-size: 1.6rem;
    line-height: 2.4rem;
    margin: 0 0 2.1rem;
    @extend %primary-font-light;

    &:last-of-type {
        margin-bottom: 0;
    }
}