// Lesson Card
// Inherits global card styling.
// Used on Topics detailed Page and Diagnostic results screen.

.card-lesson {
    &.current-lesson {
        border-bottom: 0.4rem solid;
        border-bottom-color: palette(caribbeanGreen, base);
    }
    max-width: 20rem;
    min-height: 34rem;
    margin: 0 auto;

    @include bp(tab-up) {
        max-width: 28.5rem;
        margin: 0;
    }
}

.card-lesson__content {
    .card-lesson__subtitle {
        @extend %secondary-font-light;
        font-size: 1.4rem;
        color: #757575;
        margin-bottom: 0.6rem;
    }

    .card-lesson__title {
        @extend %secondary-font-light;
        font-size: 1.8rem;
    }
    flex: 1;
    min-height: 0.9rem;
    padding: 1.8rem;

    @include bp(tab-up) {
        min-height: 12.5rem;
    }
}

.card-lesson__text {
    @extend %primary-font-light;
    font-size: 2rem;
    color: #4A4A4A;
    line-height: 2.4rem;
    flex-grow: 1;
    flex: 0 1 auto;
}

.card-lesson__tag {
    @extend %secondary-font-bold;
    font-size: 1.6rem;
    color: map-get($black, secondary);
    line-height: 2.4rem;
    display: block;
    margin-bottom: 0.9rem;
}

.card-lesson__image-ctn {
    width: 100%;
    height:auto;
    position: relative;

    &:before {
        content: '';
        display: block;
        height: 0;
        width: 100%;
        padding-bottom: 56.140351%;
    }
}
.card-lesson__state,
.card-lesson__thumbnail  {
    position:absolute;
    top:0;
    left:0;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 2px;
}

.card-lesson__state {
    display:none;
    @extend %secondary-font-bold;
    font-size: 1.6rem;
    line-height: 2.1rem;
    color: map-get($white, text);

    &:after {
        position: absolute;
        top: auto;
        bottom: 2rem;

        @include bp(tab-up) {
            bottom: 1.7rem;
        }
    }

    @include bp(tab-up) {
        line-height: 2.4rem;
        font-size: 1.8rem;
    }
}

.state__completed {
    display: flex;
    justify-content: center;

    background-color: map-get($lessonStateColours, completed);

    .card-lesson__state-text{
        @extend %primary-font-light;
        font-size: 2rem;
    }
    .material-icons {
        margin-right: 1.2rem;
    }
}

.state__next {
    display:block;
    background-color: map-get($lessonStateColours, next);

    .card-lesson__state-text {
        left: 1.8rem;

        @include bp(tab-up) {
            left: 3.2rem;
        }
    }

    .card-lesson__state-icon {
        display:none;
    }
}

.card-lesson__duration {
    flex: 1 0 5rem;
    margin-left: 1rem;
    justify-content: flex-end;
    @extend %secondary-font-bold;
    font-size: 1.5rem;
    color: map-get($color, action-dark-54);
    line-height: 2.4rem;
}

.card-lesson__duration-icon {
    margin-right: 1rem;
}

.card-lesson__actions {
    border-top: 0;
}

.card-lesson.card-lesson--condensed {
    box-shadow: none;
    border: none;
    margin-top: 1rem;
    min-height: 1rem;
    margin: 0;
    background-color: transparent;

    .card-lesson__content {
        min-height: 6rem;
        padding: 1.8rem 0;

        .card-lesson__text, .card-lesson__duration{
            font-size: 15px;
        }
    }

    footer {
        display: none;
    }
}
