.card--register__content {
    padding-top: 2.4rem;
    padding-bottom: 4rem;

    @include bp(tab-up) {
        padding-bottom: 4.3rem;
    }
}

.card--register__header {
    @extend %secondary-font-light;
    color: palette(doveGray, base);
    font-size: 1.8rem;
    line-height: (32 / 18);
    margin-bottom: 2rem;
}

.card--register__description {
    @extend %primary-font-normal;
    color: palette(doveGray, base);
    font-size: 1.6rem;
    line-height: (19 / 16);
}

.card--register__input-wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    md-input-container {
        //flex: 1 1 100%; // iOS9 / Android Stock Bug where height would become zero.
        width: 100%;

        &:not(:first-of-type) {
            margin-top: 3.2rem;
        }
    }

    @include bp(tab-up) {
        flex-direction: row;
        justify-content: space-between;

        md-input-container {
            //flex: 0 1 calc(50% - 1.1rem); // iOS9 / Android Stock Bug where height would become zero.
            width: calc(50% - 1.1rem);

            &:not(:first-of-type) {
                margin-top: 0;
            }

            &:nth-child(1n + 3) {
                margin-top: 3.2rem;
            }
        }
    }
}

.card--register-opt-in {
    .card--register__header {
        margin-bottom: 1.3rem;
    }
}

.card--register__radio-wrapper {
    margin-top: 2.7rem;

    @include bp(tab-up) {
        margin-top: 1.6rem;
    }
}

.card--register__radio-error-message {
    color: palette(sunsetOrange, base);
    margin-top: 1.6rem;
}

.card--register__radio-group {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    @include bp(tab-up) {
        flex-direction: row;
    }
}

.card--register__radio-button {
    margin: 0;
    //flex: 1 1 100%; // iOS9 / Android Stock Bug where height would become zero.
    width: 100%;

    &:not(:first-child) {
        margin-top: 1.6rem;
    }

    @include bp(tab-up) {
        //flex: 0 1 auto;  // iOS9 / Android Stock Bug where height would become zero.
        width: auto;

        &:not(:first-child) {
            margin-top: 0;
            margin-left: 5.5rem;
        }
    }

    // Width overrides fix a problem on old androids with text wrapping too soon.
    .md-label {
        color: palette(doveGray, base);
        width: calc(100% - 20px); // Accounting for the default margins around labels.

        @include bp(tab-up) {
            width: auto;
        }
    }

    // Specific styles for radio group error messages.
    .input-error & {
        .md-off {
            border-color: palette(sunsetOrange, base);
        }

        .md-label {
            color: palette(sunsetOrange, base);
        }
    }
}