@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";
@import "../../../components/miyagi-core/styles/typography";
// --------------------------
// Components
// --------------------------

@import "../../../components/miyagi-core/styles/subpage";

@import "../../../components/cards/styles/index";


$topOffset-l: 2.9rem;

// Event page is made up of an accordian,
// By default the tabs have a max-width set and don't include images in the background.
// However these tabs can be full-width meaning the majority of the page swipes. Add .miyagi-tabs__ctn-full-width
// Tabs can also have separate bg images per tab. Add .miyagi-tabs__ctn-with-image and .miyagi-tabs__bg-image--(your tab name)

.events__subpage-header__content {
    max-width: 82.5rem;
}

.events__subpage-content--no-tabs {
    margin-top: 6.3rem;
}

.events__subpage-content {

    md-tab-content {
        padding: 0;
        max-width: 100%;
    }

    md-tabs-wrapper md-prev-button,
    md-tabs-wrapper md-next-button {
        top: 2.2rem;
        height: 3.2rem;
    }

    md-icon {
        color: palette(white, base);
    }

    md-tabs.miyagi-tabs .md-tab {
        min-width: 11.2rem;
        padding-left: 0;
        padding-right:0;

        @include bp(tab-up) {
            min-width: 17.2rem;
            padding-left: 2.4rem;
            padding-right:2.4rem;
        }
    }
}

// TABS THAT HAVE A MAX-WIDTH SET.
// This is the default functionality of tabs.
.event-miyagi-tabs__tab-inner {
    padding: 0
}

// TABS THAT ARE FULL-WIDTH.
.miyagi-tabs__tab-inner-ctn-full-width {
    width: 100%;

    .event-miyagi-tabs__tab-inner {
        width: 100%;
        position: relative;
        top: 0;
        left: 50%;
        transform: translateX(-50%);

        @include bp(tab-up) {
            position: absolute;
            margin-top: -$topOffset-l;
        }
    }
}

