.events__card-content {
    padding: 0;

    .events__card-header, .events__card-body {
        width:100%;
    }

    .events__card-header {
        min-height: 87px;
        padding-top: 0;
        padding-bottom: 0;

        .events__card-title {
            padding-top: 2.7rem;
            padding-bottom: 1.4rem;
            font-size: 1.8rem;
            line-height: 2.4rem;
            color: #343434;
            margin-top: 0;
            font-family: Roboto,Arial,sans-serif;
            font-weight: 400;
        }
    }

    .events__card-image {
        display: block;
        width:100%;
    }

    .events__card-body {
        padding-top: 1rem;
        padding-bottom: 2.2rem;

        .card__action {
            padding: 0;
        }
        .card-tool__description {
            padding-top: 1.9rem;
            padding-bottom: 1.9rem;
        }
    }
}


// Seminars Tab - Blocks of text (title and  paragraph) that are not within cards.
.event-text-block {
    margin: 6rem 0 5.8rem 0;

    @include bp(tab-up) {
        margin-top: 5.4rem;
    }
}

.event-text-block_title {
    text-align: center;
    font-size: 2.4rem;
    line-height: 2.8rem;
    color: map-get($black, text);
    @extend %primary-font-light;
    margin-bottom: 1.9rem;

    @include bp(tab-up) {
        font-size: 3.6rem;
        line-height: 4.2rem;
    }
}

.event-text-block_desc {
    @extend %primary-font-light;
    color: map-get($black, secondary);
}