@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";

$cardPadding: (
    s: 0.8rem,
    m: 1.6rem,
    ml: 2.4rem,
    l: 3.2rem,
    xl: 4.8rem
);

// Current types of Cards: (Separate scss files for each are imported at the bottom of this file.)

// Partner Cards
// Tools Cards
// Event / Schedule Cards ( Schedule cards being a wrapper for a group of Event cards )
// Lesson Cards
// Progress Cards
// Diagnostics Cards


// Default md-card styles
md-card {
    margin: 0;
}

md-card > img {
    width: auto;
}



// Base Card - Used for padding / colours / font sizes only.
.card {
    background: white;
}

.card__content {
    padding: map-get($cardPadding, m);

    @include bp(tab-up) {
        padding: map-get($cardPadding, l);
    }
}

.card__actions {
    border-top: 1px solid map-get($black, divider);
    padding: 0 map-get($cardPadding, m);

    @include bp(tab-up) {
        padding: 0 map-get($cardPadding, m); // Not `l` in order to account for padding on buttons.
    }
}

.card__action {
    @extend %primary-font-bold;
    letter-spacing: 0.5px;
    line-height: 3.6rem;
    color: palette(caribbeanGreen, base);
    font-size: 1.5rem;
    font-weight: 700;
}

.card__action--replay {
    color: map-get($black, secondary);
}

.card__action--icon {
      margin-right: 0.4rem;
      top: -0.1rem;
}

.card__action.text-with-icon {
    display: inline-table;

    .card__action--icon,
    .card__action--text {
        display: inline-block;
        vertical-align: middle;
    }
}



// Groups of Cards
.card-group {
    max-width: 50rem;
    margin: auto;

    @include bp(tab-up) {
        max-width: 100%;
    }
}

@import "./partner-card";
@import "./tool-card";
@import "./event-schedule-card";
@import "./lesson-card";
@import "./progress-card";
@import "./diagnostics-card";
@import "./register-card";