$color: (
    action-colored: #FF5252,
    action-dark-15: rgba(black, 0.15),
    action-dark-24: rgba(black, 0.24),
    action-dark-32: rgba(black, 0.32),
    action-dark-54: rgba(black, 0.54),
    action-dark-87: rgba(black, 0.87),
    action-white: white,

    subpage-bg: #f5f5f5,
    divider: rgba(black, 0.12),
    hoverBg: rgba(black, 0.06),
    externalLinkIcons: rgba(black, 0.32),
    topicsHeaderBg: #37474F,
    hyperLink: #546E7A,
    hyperLinkHover: #37474F,

    secondaryBlue: #2979FF,
    slateGrey: #607D8B,
    actionColouredAccessability: #0295DA,

    mainPurple: #3F51B5,


    diagnosticsSkip: #878787,
    diagnosticsOptionDeselected: #757575,
    diagnosticsProgressInactive: rgba(255, 255, 255, 0.7),
    diagnosticsProgressActive: rgba(255, 255, 255, 1),

    /**
     * Colours on this project are getting used in more than one area
     * so storing the colour against something like `cta` or `action`
     * gives it no real meaning anymore.
     *
     * I believe it's a better idea to store the colour as what it is;
     * a unique colour with it having a suitable name. The below colours
     * is my (@Anthony) experiment into this methodology.
     *
     * http://davidwalsh.name/sass-color-variables-dont-suck
     * http://chir.ag/projects/name-that-color/
     * http://erskinedesign.com/blog/friendlier-colour-names-sass-maps/
     */
    
    alabaster: (
        base: #FAFAFA
    ),
    caribbeanGreen: (
        base: #00BFA5,
        light: lighten(#00BFA5, 8%)
    ),
    doveGray: (
        base: #616161
    ),
    emperor: (
        base: #565555
    ),
    java: (
        base: #1EBEA5
    ),
    mineShaft: (
        dark: #2D2D2D,
        base: #343434
    ),
    sanMarino: (
        base: #4054B2
    ),
    black: (
        base: #000000
    ),
    white: (
        base: #FFFFFF
    ),
    cerulean: (
        base: #03A9F4
    ),
    pomegranate: (
        base: #F44336
    ),
    shamrock: (
        base: #27BEA5
    ),
    sunsetOrange: (
        base: #FF5252
    ),
    silver: (
        base: #BBBBBB
    ),
    wildSand: (
        base: #F5F5F5
    )
);

$lessonStateColours: (
    completed: rgba(#757575, 0.80),
    next: rgba(#1EB1FC, 0.54)
);


$shadow: (
    1: #{0px 3px 1px -2px rgba(0, 0, 0, 0.14),0px 2px 2px 0px rgba(0, 0, 0, 0.098),0px 1px 5px 0px rgba(0, 0, 0, 0.084)},
    2: #{0px 2px 5px 0px rgba(0,0,0,0.26), 0px 2px 10px 0px rgba(0,0,0,0.16)},
    3: #{0px 3px 5px -1px rgba(0, 0, 0, 0.14),0px 6px 10px 0px rgba(0, 0, 0, 0.098),0px 1px 18px 0px rgba(0, 0, 0, 0.084)}
);

// UI Colours
$black: (
    text: rgba(black, 0.87),
    backdrop: rgba(black, 0.60),
    secondary: rgba(black, 0.54),
    disabled: rgba(black, 0.26),
    divider: rgba(black, 0.12),
