$white: (
    text: rgba(white, 1),
    secondary: rgba(white, 0.70),
    tertiary: rgba(white, 0.87),
    disabled: rgba(white, 0.3),
    action: rgba(white, 0.20),
    divider: rgba(white, 0.12)
