// Used for Event & Schedule Group Cards.
// Inherits global card styling.
// Used on Events Page.

.event_note--icon {
    color: map-get($black, secondary);
    font-size: 1.8rem;
    margin-right: 2.1rem;

    @include bp(tab-up) {
        margin-right: 1.7rem;
    }
}

.event_note--text {
    @extend %primary-font-bold;
    font-size: 1.2rem;
    color: map-get($black, secondary);
    line-height: 1.4rem;
}

.event__location {
    @extend %secondary-font-bold;
    font-size: 1.5rem;
    line-height: 2.4rem;
    color: map-get($black, secondary);
    margin: 0;
}

.event__date {
    @extend %secondary-font-normal;
    font-size: 1.5rem;
    color: map-get($black, secondary);
    line-height: 2.4rem;

    @include bp(tab-up) {
        margin-left: 2.5rem;
    }
}


// Event Text Card

.event-text-card  {

    .event_note--text {
        opacity: 0.87;
    }
}

.event-text-card__content {
    display: block;
    width: 100%;
    padding: map-get($cardPadding, m) map-get($cardPadding, m) 2.7rem;
    box-sizing: border-box;
    min-height: 24.3rem;
    margin:0;

    @include bp(tab-up) {
        padding: map-get($cardPadding, l) map-get($cardPadding, l) 5.3rem;
        min-height: 13.5rem;
    }
}

.event-text-card__with-header {

    .event-text-card__content {
        padding: map-get($cardPadding, ml) map-get($cardPadding, m) 2.7rem;
        min-height: 20rem;

        @include bp(tab-up) {
            padding: 1.9rem map-get($cardPadding, l) 1.7rem;
        }

    }
}

.event-text-card__header {
    padding: map-get($cardPadding, ml) map-get($cardPadding, m) 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;

    @include bp(tab-up) {
        padding: map-get($cardPadding, l) map-get($cardPadding, l) 0;
    }
}

.zone--one {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.zone--two {
    display: none;

    @include bp(tab-up) {
        display: flex;
    }
}

.event-text-card__date {
}

.event-text-card__location {
}

.event-text-card__details {
    display: flex;
    flex-direction: row;
    margin-top: 1.2rem;

    @include bp(tab-up) {
        display: none;
    }

    .event-text-card__location {
        margin-right: 1.3rem;
    }
}

.event-text-card__title {
    @extend %primary-font-light;
    color: map-get($black, text);
    font-size: 2.4rem;
    line-height: 2.8rem;
    margin: 0 0 0.7rem;

    @include bp(tab-up) {
        font-size: 3.4rem;
        line-height: 4rem;
    }
}

.event-text-card__desc {
    @extend %primary-font-light;
    color: map-get($black, secondary);
}

.event-text-card__emphasis {
    @extend %primary-font-normal;
    font-size: 1.6rem;
    color: map-get($black, secondary);
    line-height: 2.5rem;
}

.event-text-card__actions {
    height: 5.7rem;

    @include bp(tab-up) {
        height: 7.4rem;
        padding: 0 2.2rem;
    }
}

.event-text-card_group {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    align-self: stretch;

    .card {
        margin-bottom: 0.8rem;

        @include bp(tab-up) {
            width: 48%;
            width: calc(50% - 2rem);
            margin-bottom: 0;
        }
    }

    .event-text-card__desc {
        color: map-get($black, text);
        @extend %primary-font-normal;
        margin-bottom: 1.5rem;

        @include bp(tab-up) {
            margin-bottom: 4.5rem;
        }
    }

    .event-text-card__content  {
        display: flex;
        flex: 1 1 auto;
        flex-direction: column;
        align-self: stretch;
        min-height: 17rem;
        align-items: inherit;
        justify-content: center;
        padding-top: 6rem;
        padding-bottom: 6rem;


        @include bp(tab-up) {
            min-height: 0;
            justify-content: flex-start;
            padding-top: map-get($cardPadding, l);
            padding-bottom: map-get($cardPadding, ml);
        }
    }

    @include bp(tab-up) {
        flex-direction: row;
    }
}



// Schedule Card - which holds a header element and an accordian element.
.schedule-card {
    margin-bottom: 0.8rem;

    @include bp(tab-up) {
          margin-bottom: 2rem;
    }
}

.schedule-card__header {
    display: flex;
    flex-direction: row;
    align-self: stretch;
    align-items: center;

    height: 4.4rem;
    padding: 0 map-get($cardPadding, m);
    border-bottom: 1px solid map-get($color, divider);

    @include bp(tab-up) {
        height: 8rem;
        padding: 0 map-get($cardPadding, l);
    }
}

.schedule-card__date {
}

.schedule-card__actions {
    border-top: none;
    padding: 1rem 0;
}