// Partner Cards
// Inherits global card styling.
// Used on Partners Page.

.card--partner__content {
    min-height: 18.5rem;
    padding-bottom: 0;
    padding-top: 0;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    // Used if there is only a logo.
    &.card--partner__content--center {
        justify-content: center;
    }
}

.card--partner__logo {
    display: block;
    height: 5.5rem;
    max-width: 100%;
    align-self: center;

    margin-top: 2.6rem;
    margin-bottom: 2.1rem;

    .card--partner__content--center & {
        margin-top: 0;
        margin-bottom: 0;
    }
}

.card--partner__description {
    @extend %primary-font-light;
    color: map-get($color, action-dark-54);
    font-size: 1.6rem;
    line-height: (25 / 16);
}

.card--partner__actions {
    border-top: none;
    height: 5.2rem;
}

.card-partner__action {
    @extend %primary-font-med;
    font-size: 1.4rem;
}